import store from '@/store'
import User from '@/models/user'

class AuthenticationService {

    login(username, password) {
        this.logout()
        return store.getters.api.post('identity/login', {username, password})
            .then(response => {
                if (response.data.token) {
                    store.commit('tokenDetermined', response.data.token, response.data.refreshToken)
                }
                store.commit('loginSuccess', new User(username))
                return Promise.resolve(response.data.token)
            })
    }

    logout(){
        store.commit('loggedOut')
        store.commit('tokenInvalidated')
    }

}

const authenticationService = new AuthenticationService()

export default authenticationService
