import axios from 'axios'

const LOCAL_STORAGE_API_KEY = 'key';
const LOCAL_STORAGE_TOKEN = 'token';
const LOCAL_STORAGE_REFRESH_TOKEN = 'refreshToken';

const state = {
    state: {
        apiKey: null,
        token: null,
        refreshToken: null,
        axiosInstance: axios.create()
    },
    mutations: {
        baseUrlDetermined(state, baseUrl) {
            state.axiosInstance.defaults.baseURL = baseUrl
            window.console.log('API URL: ' + baseUrl)
        },
        apiKeyDetermined(state, apiKey) {
            state.apiKey = apiKey
            state.axiosInstance.defaults.headers.common['ApiKey'] = apiKey
            localStorage.setItem(LOCAL_STORAGE_API_KEY, apiKey);
            window.console.log('API key: ' + apiKey)
        },
        tokenDetermined(state, token, refreshToken) {
            if (token) {
                state.token = token;
                state.refreshToken = refreshToken;
                localStorage.setItem(LOCAL_STORAGE_TOKEN, token)
                state.axiosInstance.defaults.headers.common['Authorization'] =
                    'Bearer ' + token
                window.console.log('API token: ' + token)
            } else {
                window.console.log('No JWT token set in API')
            }
        },
        tokenInvalidated(state){
            state.token = null
            state.refreshToken = null
            localStorage.removeItem(LOCAL_STORAGE_TOKEN)
            localStorage.removeItem(LOCAL_STORAGE_REFRESH_TOKEN)
            this.commit('apiKeyDetermined', state.apiKey)
        }
    },
    getters: {
        apiKey(state) {
            return state.apiKey
        },
        api(state) {
            return state.axiosInstance
        }
    }
}

state.mutations.tokenDetermined(state.state,
    localStorage.getItem(LOCAL_STORAGE_TOKEN),
    localStorage.getItem(LOCAL_STORAGE_REFRESH_TOKEN))
state.mutations.apiKeyDetermined(state.state, localStorage.getItem(LOCAL_STORAGE_API_KEY))
state.mutations.baseUrlDetermined(state.state, process.env.VUE_APP_YOUTREAT_API_URL)

export default state