let user = null;
try {
    user = JSON.parse(localStorage.getItem('user'))
} catch (e) {
    localStorage.removeItem('user')
}

export default {
    state: {
        user: user
    },
    mutations: {
        loginSuccess(state, user) {
            state.user = user
            localStorage.setItem('user', JSON.stringify(user))
        },
        loginFailure(state) {
            state.user = null
        },
        loggedOut(state){
            state.user = null
            localStorage.removeItem('user')
        }
    },
    getters: {
        loggedIn(state) {
            return state.user != null
        },
        user(state) {
            return state.user
        }
    }
}